<script>
import PopularItem from "@/pages/aboutus/PopularItem.vue";
import api from "@/axios";
import {mapGetters} from "vuex";

export default {
  name: "Guide",
  components: {PopularItem},
  data(){
    return {
      persons: [],
    }
  },
  computed: {
    regionId() {
      return this.$route.query.region_id;
    },
    ...mapGetters(['currentLanguage']),
  },
  watch: {
    '$route.query.region_id': {
      immediate: true,
      handler(newRegionId) {
        console.log("Region ID обновился:", newRegionId);
      }
    },
    currentLanguage(newLang) {
      this.getData();
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      api.get(`/etno-center-manager/?lang_code=${this.currentLanguage}`)
          .then(response => {
            const managers = response.data;
            this.persons = managers.filter(manager => manager.etno_center_id === parseInt(this.regionId, 10));
            const test = managers.filter(manager => manager.etno_center_id)
            console.log("id id", this.regionId);
            console.log("id id", test);
          })
          .catch(error => {
            console.error(error);
          });
    },
  }
}
</script>

<template>
  <div style="display: flex; flex-direction: column; gap: 1rem">
    {{ persons }}
    {{ regionId }}
    <popular-item />
  </div>
</template>

